import { formatDistanceToNow } from 'date-fns';
import Link from 'next/link';
import Image from 'next/image';
import { Comment } from '@/types';

interface CommentsProps {
  comments: Comment[];
  maxDisplay?: number;
  showViewMore?: boolean;
}

type TimestampType = {
  toDate?: () => Date;
  seconds?: number;
  nanoseconds?: number;
} | Date | string | null | undefined;

const formatTimestamp = (timestamp: TimestampType) => {
  if (!timestamp) return 'sometime ago';
  
  if (typeof timestamp === 'object' && 'toDate' in timestamp && timestamp.toDate) {
    return formatDistanceToNow(timestamp.toDate(), { addSuffix: true });
  }
  
  if (timestamp instanceof Date) {
    return formatDistanceToNow(timestamp, { addSuffix: true });
  }
  
  if (typeof timestamp === 'object' && 'seconds' in timestamp && timestamp.seconds !== undefined) {
    return formatDistanceToNow(new Date(timestamp.seconds * 1000), { addSuffix: true });
  }
  
  if (typeof timestamp === 'string') {
    return formatDistanceToNow(new Date(timestamp), { addSuffix: true });
  }

  return 'sometime ago';
};

export default function Comments({ comments, maxDisplay = 2, showViewMore = true }: CommentsProps) {
  const displayComments = maxDisplay ? comments.slice(0, maxDisplay) : comments;
  const hasMoreComments = comments.length > maxDisplay;

  return (
    <div className="space-y-2 px-3">
      {displayComments.map((comment) => (
        <div key={comment.id} className="flex space-x-2">
          <div className="h-6 w-6 relative flex-shrink-0">
            <Image
              src={comment.authorPhotoURL || '/images/default-avatar.png'}
              alt={comment.authorName}
              className="rounded-full"
              fill
              sizes="24px"
            />
          </div>
          <div className="flex-1">
            <p className="text-sm">
              <Link
                href={`/profile/${comment.authorId}`}
                className="font-semibold hover:underline text-blue-600"
              >
                {comment.authorName}
              </Link>{' '}
              <span className="text-gray-700">{comment.content}</span>
            </p>
            <p className="text-xs text-gray-400 mt-0.5">
              {formatTimestamp(comment.createdAt)}
            </p>
          </div>
        </div>
      ))}
      {showViewMore && hasMoreComments && (
        <button className="text-gray-500 text-sm hover:text-gray-700">
          View all {comments.length} comments
        </button>
      )}
    </div>
  );
}